import React from "react";
import { app, createComponent } from "webiny-app";
import styles from "./WidgetHolder.scss";
import EditorWidget from "./EditorWidget";

class WidgetHolder extends React.Component {
    constructor() {
        super();

        this.cms = app.services.get("cms");
    }

    render() {
        const { widget } = this.props;
        const data = widget.type === "global" ? widget.data : widget;

        let editorWidget = this.cms.getEditorWidget(data.type);
        if (!editorWidget) {
            return null;
        }

        return (
            <div className={styles.editorWidgetHolder}>
                {this.props.children({
                    data,
                    widget: editorWidget.renderWidget({ EditorWidget, data })
                })}
            </div>
        );
    }
}

export default createComponent(WidgetHolder);
